  content: "";
  clear: both;
  height: 0; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

@font-face {
  font-family: 'Coolvetica';
  src: url("fonts/coolvetica.ttf"); }

@font-face {
  font-family: 'Fleltex';
  src: url("fonts/fleltex.ttf"); }

html {
  font-size: 62.5%; }

body {
  font-family: "Fleltex";
  font-size: 1.6rem;
  color: #09090e; }

h1 {
  font-family: "Fleltex", serif;
  font-size: 1.7rem;
  font-weight: 400; }

*::selection {
  background: #fff0f5; }

html,
body {
  height: 100%; }

body {
  width: 100%;
  min-height: 100%;
  margin: 0;
  font-family: 'Fleltex';
  background: #3d3b3b;
  height: 100vh;
  color: #f7f7f7;
  overflow: hidden;
  background: linear-gradient(-45deg, #011638, #9055A2, #2E294E, #011638);
  background-size: 400% 400%;
  animation: bodyGradient 15s ease infinite; }

.wrapper {
  max-width: 100%; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

p {
  margin: 0; }

a {
  text-decoration: none;
  color: #2c638f; }

img {
  display: block; }

ul {
  padding: 0;
  list-style: none; }

#sb-pane {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100%;
  font-size: 3em;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6); }

#sb {
  animation: fadeInRight 1s; }

#sb-title {
  margin-top: 10px;
  font-size: 0.5em; }

#sb-social {
  display: flex;
  animation: fadeInLeft 1s;
  animation-fill-mode: forwards;
  animation-delay: 0.70s;
  opacity: 0; }
  #sb-social > .sb-social-network {
    margin-top: 20px;
    width: 60px;
    height: 60px; }
    #sb-social > .sb-social-network > .sb-social-network-container {
      width: 51px;
      height: 50px;
      border-radius: 50%;
      background: white;
      position: absolute;
      box-shadow: 3px 4px 4px rgba(0, 0, 0, 0.2); }
      #sb-social > .sb-social-network > .sb-social-network-container > img {
        position: relative;
        top: 5px;
        left: 6px;
        height: 40px;
        width: 40px; }
        #sb-social > .sb-social-network > .sb-social-network-container > img#stack-overflow {
          top: 10px;
          left: 11px;
          width: 30px;
          height: 30px; }

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0); }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-100%, 0, 0); }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

@keyframes bodyGradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@media (max-width: 650px) {
  #sb-title {
    margin-top: 30px; }
  #sb-social {
    margin-top: 30px; } }
